<template>
  <b-container class="bv-example-row">
    <h1>Top gainers KLSE stock market</h1>
    <p>Generated on {{stockDate}}</p>
    <!-- <b-row>
      <b-col>
        <TradingViewSmall chartId="1" counter="2" />
      </b-col>
    </b-row>-->
    <b-row>
      <b-col sm="5" md="6" class="my-1">
        <b-form-group
          label="Filter"
          label-cols-sm="3"
          label-align-sm="left"
          label-size="sm"
          label-for="filterInput"
          class="mb-0"
        >
          <b-input-group size="sm">
            <b-form-input
              v-model="filter"
              type="search"
              id="filterInput"
              placeholder="Type to Search"
            ></b-form-input>
            <b-input-group-append>
              <b-button :disabled="!filter" @click="filter = ''">Clear</b-button>
            </b-input-group-append>
          </b-input-group>
        </b-form-group>
      </b-col>
      <b-col sm="5" md="6" class="my-1">
      
      </b-col>

      <b-col sm="5" md="6" class="my-1">
        <b-form-group
          label="Per page"
          label-cols-sm="6"
          label-cols-md="4"
          label-cols-lg="3"
          label-align-sm="left"
          label-size="sm"
          label-for="perPageSelect"
          class="mb-0"
        >
          <b-form-select v-model="perPage" id="perPageSelect" size="sm" :options="pageOptions"></b-form-select>
        </b-form-group>
      </b-col>

      <b-col sm="5" md="6" class="my-1">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="perPage"
          align="fill"
          size="sm"
          class="my-0"
        ></b-pagination>
      </b-col>
    </b-row>

    <!-- Main table element -->
    <b-table
      show-empty
      small
      stacked="md"
      :items="stocks"
      :fields="fields"
      :current-page="currentPage"
      :per-page="perPage"
      :filter="filter"
      :filterIncludedFields="filterOn"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :sort-direction="sortDirection"
      @filtered="onFiltered"
    >
      <!-- <template v-slot:cell(name)="row">{{ row.value.first }} {{ row.value.last }}</template> -->

      <template v-slot:cell(actions)="row">
        <b-button
          size="sm"
          @click="info(row.item, row.index, $event.target)"
          class="mr-1"
        >Quick Info</b-button>
        <b-button
          size="sm"
          @click="row.toggleDetails"
        >{{ row.detailsShowing ? 'Hide' : 'Show' }} Details</b-button>
      </template>

      <template v-slot:row-details="row">
        <b-card>
          <ul>
            <li v-for="(value, key) in row.item" :key="key">{{ key }}: {{ value }}</li>
          </ul>
        </b-card>
      </template>
    </b-table>

    <!-- Info modal -->
    <b-modal :id="infoModal.id" :title="infoModal.title" ok-only @hide="resetInfoModal">
      <!-- <pre>{{ infoModal.content }}</pre> -->
      <h1>{{infoModal.stockName}}</h1>
      <b-button :href="infoModal.tradingViewLink" target="_blank">Open Screener</b-button>
    </b-modal>
  </b-container>
  <!-- <b-container class="bv-example-row">
    <b-row align-v="center">
      <b-col>1</b-col>
      <b-col>1</b-col>
      <b-col>1</b-col>
    </b-row>
    <b-row>
      <b-col>
        <ul>
          <li v-for="stock in stocks" v-bind:key="stock.Name">{{stock.Name}}</li>
        </ul>
      </b-col>
    </b-row>
  </b-container>-->
</template>

<script>
import TradingViewSmall from "@/components/TradingViewSmall.vue";
import db from "../../config/firebaseStore";
import moment from 'moment'

export default {
  data() {
    return {
      fields: [
        {
          key: "Name",
          label: "Name",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "Code",
          label: "Bursa Code",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "Price",
          label: "Price",
          sortable: true,
          class: "text-center"
        },
        {
          key: "ChangePercent",
          label: "ChangePercent",
          sortable: true,
          class: "text-center"
        },
        {
          key: "ChangeAmount",
          label: "ChangeAmount",
          sortable: true,
          class: "text-center"
        },
        {
          key: "Volume",
          label: "Volume",
          sortable: true,
          class: "text-center"
        },
        {
          key: "IsSyariah",
          label: "is Syariah",
          formatter: (value, key, item) => {
            return value ? "Yes" : "No";
          },
          sortable: true,
          sortByFormatted: true,
          filterByFormatted: true
        },
        { key: "actions", label: "Actions" }
      ],
      stocks: [],
      stockDate: "",
      totalRows: 1,
      currentPage: 1,
      perPage: 50,
      pageOptions: [5, 10, 50, 100],
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      filterOn: [],
      infoModal: {
        id: "info-modal",
        title: "",
        content: "",
        stockName: "",
        chartId: "",
        tradingViewLink: ""
      }
    };
  },
  computed: {
    sortOptions() {
      // Create an options list from our fields
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key };
        });
    }
  },
  // mounted() {
  //   // Set the initial number of items
  // },
  created() {
    console.log("created");
    db.collection("stocks")
      .orderBy("GenerateDate", "desc")
      .limit(1)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = doc.data();
          this.stocks = JSON.parse(data.Data);
          this.stockDate = data.GenerateDate.toDate();
          this.totalRows = this.stocks.length;
        });
      });
  },
  components: {
    TradingViewSmall: TradingViewSmall
  },
  methods: {
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`;
      this.infoModal.content = JSON.stringify(item, null, 2);
      this.infoModal.stockName = item.Name;
      this.infoModal.chartId = "chart_" + item.Name;
      this.infoModal.tradingViewLink =
        "https://www.tradingview.com/symbols/MYX-" + item.NameRoot + "/";
      this.$root.$emit("bv::show::modal", this.infoModal.id, button);
    },
    resetInfoModal() {
      this.infoModal.title = "";
      this.infoModal.content = "";
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    rowClass(item, type) {
      if (!item || type !== "row") return;
      if (item.status === "awesome") return "table-success";
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



