<template>
  <div>
    <p>Trading vue component</p>
    <!-- TradingView Widget BEGIN -->
    <div class="tradingview-widget-container">
      <h1>{{chartId}}</h1>
      <h1>{{counter}}</h1>
      <div id=chartcontainer2></div>
      <!-- <div id={{chartId}}></div> -->
      <div class="tradingview-widget-copyright">
        <a href="https://www.tradingview.com/symbols/NASDAQ-AAPL/" rel="noopener" target="_blank">
          <span class="blue-text">AAPL Chart</span>
        </a> by TradingView
      </div>
    </div>
    <!-- TradingView Widget END -->
  </div>
</template>

<script>
export default {
  props: ["chartId", "counter"],
  data() {
    return {
      containerId: this.chartId
    };
  },
  mounted() {
    console.log(this.chartId);
    // console.log({{counter}});
    // setTimeout(function() {
    // TradingView code

    TradingView.onready(function() {
      new TradingView.widget({
        width: 980,
        height: 610,
        symbol: "PCHEM",
        interval: "D",
        timezone: "Etc/UTC",
        theme: "light",
        style: "1",
        locale: "en",
        toolbar_bg: "#f1f3f6",
        enable_publishing: false,
        allow_symbol_change: true,
        container_id: "chartcontainer2"
      });
    });

    // TradingView.onready(function() {
    //   new TradingView.widget({
    //     width: 980,
    //     height: 610,
    //     symbol: "NASDAQ:AAPL",
    //     interval: "D",
    //     timezone: "Etc/UTC",
    //     theme: "light",
    //     style: "1",
    //     locale: "en",
    //     toolbar_bg: "#f1f3f6",
    //     enable_publishing: false,
    //     allow_symbol_change: true,
    //     container_id: "chartcontainer2"
    //   });
    // });
    // }, 1000);
  }
};
</script>

<style scoped>
</style>



